* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: "montseratt", sans-serif;
	background-color: #786bc5;
	background-image: url("./assets/bg3.jpg");
}

/* Applies depending on device type or when device screen is at least said width */
@media only screen and (min-width: 1025px) {
	body {
		margin-left: 600px;
		margin-right: 600px;
	}
}

.App {
	background-size: cover;
	background-position: bottom;
	transition: 0.4s ease-out;
}

.warm {
	background-image: url("./assets/warm-bg.jpg");
}

.cold {
	background-image: url("./assets/cold-bg.jpg");
}

main {
	min-height: 100vh;
	background-image: linear-gradient(
		to bottom,
		rgba(0, 0, 0, 0.2),
		rgba(0, 0, 0, 0.75)
	);
	padding: 25px;
}

.search-box {
	width: 100%;
	margin: 0 0 75px;
}

.search-box .search-bar {
	display: block;
	width: 100%;
	padding: 15px;
	appearance: none;
	background: none;
	border: none;
	outline: none;
	background-color: rgba(255, 255, 255, 0.5);
	border-radius: 0px 0px 16px 16px;
	margin-top: -25px;
	box-shadow: 0px 5px black;
	color: #313131;
	font-size: 20px;
	transition: 0.4s ease;
}

.search-box .search-bar:focus {
	background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
	color: white;
	font-size: 32 px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
	color: white;
	font-size: 32 px;
	font-style: italic;
	text-align: center;
	text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.checkbox {
	margin-top: 10px;
	margin-bottom: -20px;

	text-align: center;
	background-image: linear-gradient(
		to left,
		violet,
		indigo,
		green,
		blue,
		rgb(141, 141, 80),
		orange,
		red
	);
	-webkit-background-clip: text;
	-moz-background-clip: text;
	background-clip: text;
	color: transparent;
}
.checkbox > div {
	margin: 0 auto;
	text-align: center;
}
.weather-box {
	text-align: center;
}

.weather-box .temp {
	position: relative;
	display: inline-block;
	margin: 30px auto;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 16px;

	padding: 15px 25px;

	color: white;
	font-size: 102px;
	font-weight: 900;

	text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
	text-align: center;
	box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
	color: white;
	font-size: 48px;
	font-weight: 700;

	text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}

.weather-box .time {
	color: rgb(209, 195, 195);
	margin-top: 15px;
	font-size: 32 px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
